export default [
    {
        path: "/css/index",
        name: "index",
        component: () => import("@/pages/CSS/index"),
        meta: { title: "CSS 教程" }
    },
    {
        path: "/css/css_jianjie",
        name: "css_jianjie",
        component: () => import("@/pages/CSS/css_jianjie"),
        meta: { title: "CSS 简介" }
    },
    {
        path: "/css/css_syntax",
        name: "css_syntax",
        component: () => import("@/pages/CSS/css_syntax"),
        meta: { title: "CSS 基本语法" }
    },
    {
        path: "/css/css_syntax_pro",
        name: "css_syntax_pro",
        component: () => import("@/pages/CSS/css_syntax_pro"),
        meta: { title: "CSS 高级语法" }
    },
    {
        path: "/css/css_syntax_descendant_selector",
        name: "css_syntax_descendant_selector",
        component: () => import("@/pages/CSS/css_syntax_descendant_selector"),
        meta: { title: "CSS 派生选择器" }
    },
    {
        path: "/css/css_syntax_id_selector",
        name: "css_syntax_id_selector",
        component: () => import("@/pages/CSS/css_syntax_id_selector"),
        meta: { title: "CSS id选择器" }
    },
    {
        path: "/css/css_syntax_class_selector",
        name: "css_syntax_class_selector",
        component: () => import("@/pages/CSS/css_syntax_class_selector"),
        meta: { title: "CSS 类选择器" }
    },
    {
        path: "/css/css_syntax_attribute_selector",
        name: "css_syntax_attribute_selector",
        component: () => import("@/pages/CSS/css_syntax_attribute_selector"),
        meta: { title: "CSS 属性选择器" }
    },
    {
        path: "/css/css_howto",
        name: "css_howto",
        component: () => import("@/pages/CSS/css_howto"),
        meta: { title: "CSS 创建" }
    }
];



